html {
  box-sizing: border-box;
}

fieldset {
  border: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

:root {
  --primary-colour: #aca6a1;
  --secondary-colour: #2d2d33;
  --button-margin: 3rem;
}

.colour--green {
  color: var(--green) !important;
}

.colour--white {
  color: #fff !important;
}

.colour--secondary {
  color: #2d2d33 !important;
}

body {
  background: #f5f4f2;
}

body.background-white {
  background: #fff;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.embed-container {
  max-width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
}

.embed-container iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

html {
  font-size: 10px;
}

html .type {
  margin-bottom: 0;
}

body {
  color: #2d2d33;
  font-family: sofia-pro, sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
}

h1, .h1 {
  font-size: var(--h1-size);
  letter-spacing: .5px;
  margin-bottom: 2rem;
  line-height: 1.1;
}

@media screen and (width <= 768px) {
  h1, .h1 {
    font-size: 3.6rem;
  }
}

h2, .h2 {
  font-size: var(--h2-size);
  letter-spacing: .5px;
  margin-bottom: 1.5rem;
}

h3, .h3 {
  font-size: var(--h3-size);
  letter-spacing: .5px;
  margin-bottom: 1.5rem;
  line-height: 1.3;
}

h4, .h4 {
  font-size: var(--h4-size);
  letter-spacing: .5px;
  margin-bottom: 1.5rem;
  line-height: 1.3;
}

h5, .h5 {
  font-size: var(--h5-size);
  letter-spacing: .5px;
  margin-bottom: 1.5rem;
  line-height: 1.3;
}

h6, .h6 {
  font-size: var(--h6-size);
  margin-bottom: 1.5rem;
  line-height: 1.3;
}

p, li {
  font-size: var(--p-size);
  color: #979797;
  letter-spacing: .3px;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

b, strong {
  font-family: sofia-pro, sans-serif;
  font-weight: 700;
}

i, em {
  font-style: italic !important;
}

.center, .text-center {
  text-align: center;
}

.right, .text-right {
  text-align: right;
}

hr {
  border: 0;
  border-bottom: 1px solid #cfc7c0;
  width: 100%;
  margin: 4rem auto;
}

.small {
  font-size: 80%;
}

.smaller {
  font-size: 60%;
}

.no-margin {
  margin: 0;
}

.white {
  color: #fff;
}

.uppercase {
  text-transform: uppercase;
}

a.link, a:not([class]) {
  box-shadow: inset 0 -1px 0 var(--green), 0 1px 0 var(--green);
  color: var(--green);
  text-decoration: none;
  transition: all .333s;
  overflow: hidden;
}

a.link:hover, a:not([class]):hover {
  box-shadow: inset 0 -30px 0 var(--green), 0 1px 0 var(--green);
  color: #fff !important;
}

.arrow-link {
  color: var(--green);
  font-family: sofia-pro, sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  text-decoration: none;
  position: relative;
}

.arrow-link:after {
  content: "";
  background-image: url("data:image/svg+xml; utf8, <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"40.05\" height=\"24.042\"><path fill=\"%23367b7f\" fill-rule=\"nonzero\" d=\"m28.029 0 12.02 12.02-12.02 12.022-1.414-1.415 9.623-9.624L0 12.707l.016-2 36.187.295-9.588-9.588L28.029 0Z\"/></svg>");
  background-repeat: no-repeat;
  background-size: 40.05px 24.042px;
  width: 40.05px;
  height: 24.042px;
  margin-left: 1.4rem;
  transition: all .2s cubic-bezier(.365, .04, .44, 1);
  display: inline-block;
  position: relative;
  top: 6px;
}

.arrow-link:hover:after {
  transform: translateX(.3rem);
}

.colour--primary {
  color: var(--green);
}

.animate-white-to-green {
  background-image: linear-gradient(90deg, var(--green) 50%, white 50%);
  -webkit-text-fill-color: transparent;
  background-position: 100%;
  background-size: 200% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  transition: all .5s cubic-bezier(.365, .04, .44, 1);
}

.animate-white-to-green:hover {
  background-position: 0;
}

.animate-white-to-transparent-white {
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(90deg, #ffffff80 50%, #fff 50%);
  background-position: 100%;
  background-size: 200% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  transition: all .5s cubic-bezier(.365, .04, .44, 1);
}

.animate-white-to-transparent-white:hover {
  background-position: 0;
}

.underline {
  display: inline-block;
  position: relative;
}

.underline:after {
  content: "";
  background: #ffffff80;
  width: 100%;
  height: 1px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.yotpo-default-button, .shopify-challenge__button, .button {
  cursor: pointer;
  background: var(--green);
  color: #fff;
  text-align: center;
  border: 0;
  border-radius: 10000rem;
  padding: 1.25rem 5rem 1.65rem;
  font-size: 1.6rem;
  text-decoration: none;
  transition: all .111s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px #0000000d;
}

.yotpo-default-button:before, .shopify-challenge__button:before, .button:before {
  content: "";
  transform-origin: 100% 100%;
  background: #ffffff26;
  transition: transform .5s cubic-bezier(.165, .84, .44, 1);
  display: block;
  position: absolute;
  inset: 0;
  transform: scaleX(0);
}

.yotpo-default-button:hover:before, .shopify-challenge__button:hover:before, .button:hover:before {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.yotpo-default-button:active, .shopify-challenge__button:active, .button:active {
  transform: translateY(1px);
}

.yotpo-default-button.wide, .shopify-challenge__button.wide, .button.wide {
  padding: 1.25rem 8rem 1.65rem;
}

.yotpo-default-button.full, .shopify-challenge__button.full, .button.full {
  display: block;
}

.shopify-challenge__button {
  font-family: sofia-pro, sans-serif;
  font-weight: 600;
  margin: 2rem auto !important;
}

@keyframes spinner {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader, .loader:after {
  border-radius: 50%;
  width: 2.4rem;
  height: 2.4rem;
}

.loader {
  text-indent: -9999em;
  border: .24rem solid #fff3;
  border-left-color: #fff;
  margin: 0 auto;
  font-size: 10px;
  animation: 1s linear infinite spinner;
  display: none;
  position: absolute;
  top: 14px;
  left: 0;
  right: 0;
  transform: translateZ(0);
}

.busy {
  pointer-events: none;
  position: relative;
  color: var(--green) !important;
}

.busy .loader {
  display: block;
}

.h3.afterpay-modal-headline {
  font-size: 2.4rem !important;
}

body .notyf__ripple {
  background: var(--green) !important;
}

body .notyf__message a {
  color: var(--green);
  text-decoration: none;
  transition: all .333s;
  overflow: hidden;
  box-shadow: inset 0 -1px #fff, 0 1px #fff;
  color: #fff !important;
}

body .notyf__message a:hover {
  box-shadow: inset 0 -30px #fff, 0 1px #fff;
  color: var(--green) !important;
}

@keyframes notyf-fadeinup {
  0% {
    opacity: 0;
    transform: translateY(25%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes notyf-fadeinleft {
  0% {
    opacity: 0;
    transform: translateX(25%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes notyf-fadeoutright {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(25%);
  }
}

@keyframes notyf-fadeoutdown {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(25%);
  }
}

@keyframes ripple {
  0% {
    transform: scale(0)translateY(-45%)translateX(13%);
  }

  to {
    transform: scale(1)translateY(-45%)translateX(13%);
  }
}

.notyf {
  color: #fff;
  z-index: 9999;
  pointer-events: none;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.notyf__icon--error, .notyf__icon--success {
  background: #fff;
  border-radius: 50%;
  width: 21px;
  height: 21px;
  margin: 0 auto;
  display: block;
  position: relative;
}

.notyf__icon--error:after, .notyf__icon--error:before {
  content: "";
  background: currentColor;
  border-radius: 3px;
  width: 3px;
  height: 12px;
  display: block;
  position: absolute;
  top: 5px;
  left: 9px;
}

.notyf__icon--error:after {
  transform: rotate(-45deg);
}

.notyf__icon--error:before {
  transform: rotate(45deg);
}

.notyf__icon--success:after, .notyf__icon--success:before {
  content: "";
  background: currentColor;
  border-radius: 3px;
  width: 3px;
  display: block;
  position: absolute;
}

.notyf__icon--success:after {
  height: 6px;
  top: 9px;
  left: 6px;
  transform: rotate(-45deg);
}

.notyf__icon--success:before {
  height: 11px;
  top: 5px;
  left: 10px;
  transform: rotate(45deg);
}

.notyf__toast {
  pointer-events: auto;
  box-sizing: border-box;
  border-radius: 2px;
  flex-shrink: 0;
  max-width: 300px;
  padding: 0 15px;
  animation: .3s ease-in forwards notyf-fadeinup;
  display: block;
  position: relative;
  overflow: hidden;
  transform: translateY(25%);
  box-shadow: 0 3px 7px #00000040;
}

.notyf__toast--disappear {
  animation: .3s .25s forwards notyf-fadeoutdown;
  transform: translateY(0);
}

.notyf__toast--disappear .notyf__icon, .notyf__toast--disappear .notyf__message {
  opacity: 1;
  animation: .3s forwards notyf-fadeoutdown;
  transform: translateY(0);
}

.notyf__toast--disappear .notyf__dismiss {
  opacity: 1;
  animation: .3s forwards notyf-fadeoutright;
  transform: translateX(0);
}

.notyf__toast--disappear .notyf__message {
  animation-delay: 50ms;
}

.notyf__toast--upper {
  margin-bottom: 20px;
}

.notyf__toast--lower {
  margin-top: 20px;
}

.notyf__toast--dismissible .notyf__wrapper {
  padding-right: 30px;
}

.notyf__ripple {
  transform-origin: 100% 100%;
  z-index: 5;
  border-radius: 50%;
  width: 400px;
  height: 400px;
  animation: .4s ease-out forwards ripple;
  position: absolute;
  top: 0;
  right: 0;
  transform: scale(0)translateY(-51%)translateX(13%);
}

.notyf__wrapper {
  z-index: 10;
  border-radius: 3px;
  align-items: center;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-right: 15px;
  display: flex;
  position: relative;
}

.notyf__icon {
  text-align: center;
  opacity: 0;
  width: 22px;
  margin-right: 13px;
  font-size: 1.3em;
  animation: .3s .3s forwards notyf-fadeinup;
}

.notyf__dismiss {
  opacity: 0;
  width: 26px;
  height: 100%;
  margin-right: -15px;
  animation: .3s .35s forwards notyf-fadeinleft;
  position: absolute;
  top: 0;
  right: 0;
}

.notyf__dismiss-btn {
  cursor: pointer;
  opacity: .35;
  background-color: #00000040;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  transition: opacity .2s, background-color .2s;
}

.notyf__dismiss-btn:after, .notyf__dismiss-btn:before {
  content: "";
  background: #fff;
  border-radius: 3px;
  width: 2px;
  height: 12px;
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% - 1px);
}

.notyf__dismiss-btn:after {
  transform: rotate(-45deg);
}

.notyf__dismiss-btn:before {
  transform: rotate(45deg);
}

.notyf__dismiss-btn:hover {
  opacity: .7;
  background-color: #00000026;
}

.notyf__dismiss-btn:active {
  opacity: .8;
}

.notyf__message {
  vertical-align: middle;
  opacity: 0;
  line-height: 1.5em;
  animation: .3s .25s forwards notyf-fadeinup;
  position: relative;
}

@media only screen and (width <= 480px) {
  .notyf {
    padding: 0;
  }

  .notyf__ripple {
    width: 600px;
    height: 600px;
    animation-duration: .5s;
  }

  .notyf__toast {
    border-radius: 0;
    width: 100%;
    max-width: none;
    box-shadow: 0 -2px 7px #00000021;
  }

  .notyf__dismiss {
    width: 56px;
  }
}

.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus {
  outline: 0;
}

.flickity-viewport {
  height: 100%;
  position: relative;
  overflow: hidden;
}

.flickity-slider {
  width: 100%;
  height: 100%;
  position: absolute;
}

.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.flickity-button {
  color: #333;
  background: #ffffffbf;
  border: none;
  position: absolute;
}

.flickity-button:hover {
  cursor: pointer;
  background: #fff;
}

.flickity-button:focus {
  outline: 0;
  box-shadow: 0 0 0 5px #19f;
}

.flickity-button:active {
  opacity: .6;
}

.flickity-button:disabled {
  opacity: .3;
  cursor: auto;
  pointer-events: none;
}

.flickity-button-icon {
  fill: currentColor;
}

.flickity-prev-next-button {
  border-radius: 50%;
  width: 44px;
  height: 44px;
  top: 50%;
  transform: translateY(-50%);
}

.flickity-prev-next-button.previous {
  left: 10px;
}

.flickity-prev-next-button.next {
  right: 10px;
}

.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}

.flickity-rtl .flickity-prev-next-button.next {
  left: 10px;
  right: auto;
}

.flickity-prev-next-button .flickity-button-icon {
  width: 60%;
  height: 60%;
  position: absolute;
  top: 20%;
  left: 20%;
}

.flickity-page-dots {
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 0;
  line-height: 1;
  list-style: none;
  position: absolute;
  bottom: -25px;
}

.flickity-rtl .flickity-page-dots {
  direction: rtl;
}

.flickity-page-dots .dot {
  opacity: .25;
  cursor: pointer;
  background: #333;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  display: inline-block;
}

.flickity-page-dots .dot.is-selected {
  opacity: 1;
}

#shopify-section-header {
  z-index: 9;
  position: sticky;
  top: 0;
}

.header {
  z-index: 9999;
  border-bottom: 1px solid #0000;
  margin-bottom: -7.55rem;
  transition: all .5s cubic-bezier(.365, .04, .44, 1);
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
}

@media only screen and (width <= 1023px) {
  .header {
    margin-bottom: -7.95rem;
  }
}

@media only screen and (width <= 767px) {
  .header {
    margin-bottom: -11.95rem;
  }
}

.header:after {
  content: "";
  opacity: 0;
  transition: all .5s cubic-bezier(.365, .04, .44, 1);
  display: block;
  position: absolute;
  inset: 0 0 -1rem;
}

.header.nav-down, .header.nav-up {
  border-bottom: 1px solid var(--green);
  background: #a6d3d8;
}

.header.nav-down .header-logo svg, .header.nav-up .header-logo svg {
  transform: scale(.8);
}

.header-wrap {
  z-index: 2;
  max-width: var(--content-width);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 4rem;
  display: flex;
  position: relative;
}

@media only screen and (width <= 1023px) {
  .header-wrap {
    padding: 0 2rem;
  }
}

.header-nav {
  flex-wrap: wrap;
  padding: 0 1rem;
  display: flex;
  position: relative;
}

@media only screen and (width <= 1023px) {
  .header-nav {
    display: none;
  }
}

.header-nav:after {
  content: "";
  background: var(--green);
  opacity: .8;
  opacity: 0;
  border-radius: 10000rem;
  transition: all .2s cubic-bezier(.365, .04, .44, 1) .3s;
  display: block;
  position: absolute;
  inset: 0;
  box-shadow: 0 10px 30px #0000000d;
}

.header-nav.coloured .animate-white-to-green {
  background-image: linear-gradient(90deg, #ffffff80 50%, #fff 50%);
}

.header-nav.coloured:after {
  opacity: .8;
}

.header-nav__item {
  z-index: 3;
  position: relative;
}

.header-nav__item__link {
  padding: 1.15rem 1.5rem 1.4rem;
  font-family: sofia-pro, sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
}

.header-nav__item__link:before {
  content: "";
  background: #fff;
  border-radius: 1rem;
  width: 1rem;
  height: 1rem;
  margin: 0 auto;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header-logo {
  width: 20rem;
  margin-top: .5rem;
  margin-bottom: .5rem;
  margin-left: -16rem;
}

.header-logo svg {
  width: 7.5rem;
  height: auto;
  transition: all .2s cubic-bezier(.365, .04, .44, 1);
}

@media only screen and (width <= 1023px) {
  .header-logo {
    width: 10rem;
    margin-left: 0;
  }

  .header-logo svg {
    width: 8rem;
    height: auto;
  }
}

.header-actions {
  flex-wrap: wrap;
  justify-content: center;
  width: 12rem;
  height: 5.1rem;
  padding: 0 1rem;
  display: flex;
  position: relative;
}

@media only screen and (width <= 1023px) {
  .header-actions {
    width: 16rem;
  }
}

.header-actions:after {
  content: "";
  background: var(--green);
  opacity: .8;
  border-radius: 10000rem;
  display: block;
  position: absolute;
  inset: 0;
  box-shadow: 0 10px 30px #0000000d;
}

@media only screen and (width <= 1023px) {
  .header-actions:after {
    opacity: 1;
  }
}

.header-actions__navigation, .header-actions__search, .header-actions__cart {
  z-index: 2;
  padding: 1.2rem 1.3rem;
  text-decoration: none;
  transition: all .2s cubic-bezier(.365, .04, .44, 1);
  position: relative;
}

.header-actions__navigation:hover, .header-actions__search:hover, .header-actions__cart:hover {
  transform: scale(.9);
}

.header-actions__navigation svg, .header-actions__search svg, .header-actions__cart svg {
  position: relative;
  top: 2px;
}

.header-actions__navigation__count, .header-actions__search__count, .header-actions__cart__count {
  width: 1.6rem;
  height: 1.4rem;
  color: var(--green);
  text-align: center;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  border-radius: .2rem;
  height: 1.3rem;
  font-size: 1rem;
  line-height: 1.2rem;
  transition: all .5s cubic-bezier(.365, .04, .44, 1);
  position: absolute;
  top: 2rem;
  right: 1.45rem;
  scale: 0;
}

.header-actions__navigation__count:after, .header-actions__search__count:after, .header-actions__cart__count:after {
  content: "";
  border-radius: 100rem;
  width: 1rem;
  height: 1rem;
  margin-top: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.header-actions__navigation__count.pulse:after, .header-actions__search__count.pulse:after, .header-actions__cart__count.pulse:after {
